.sf-user-short-info{display:flex;align-items:center;width:100%;color:#343a40}td .sf-user-short-info{min-width:200px}.sf-user-short-info__name{max-width:100%;text-align:left}.sf-user-short-info__avatar{flex:0 0 auto;margin-right:0.5rem;display:flex;justify-content:center;align-items:center;width:2rem;height:2rem;overflow:hidden;border-radius:50%;background-color:#f8f9fa;box-shadow:inset 0 0 0 1px rgba(0,0,0,0.1);color:#6c757d;font-weight:700}.sf-user-short-info__avatar>img{width:100%;height:100%;-o-object-fit:contain;object-fit:contain}
