.sf-nav{width:100%}.sf-nav__list{width:100%;padding:0;margin:0;list-style:none}.sf-nav__item{margin:0}.sf-nav__item-heading{display:flex;align-items:center;padding:0.75rem 1rem;color:#fff;cursor:pointer}a.sf-nav__item-heading:hover{color:#fff;text-decoration:none}a.sf-nav__item-heading.active{background-color:#497cb1}.sf-nav__item-heading[data-sf]::after{content:'\f105';flex-shrink:0;margin-left:auto;margin-right:-0.25rem;font-family:'Font Awesome 5 Free';width:1rem;text-align:center;font-weight:900;opacity:0.3;transition:transform 0.25s ease-in-out}.sf-nav__item-heading.active[data-sf]::after{transform:rotate(90deg)}.sf-nav__item-heading>.fa{flex-shrink:0;width:1.75rem;opacity:0.3}.sf-nav__item-heading>span{margin-right:0.5rem;max-width:100%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:0.875rem}.sf-nav__item-content{max-height:0;overflow:hidden;background-color:#343a40}body.loaded .sf-nav__item-content{transition:max-height 0.25s ease-in-out}.sf-navgroup{width:100%}.sf-navgroup__title{margin-top:0.5rem;padding:0.5rem 1rem;font-size:0.6875rem;font-weight:500;text-transform:uppercase;color:#fff;opacity:0.5}.sf-subnav__item{display:flex;align-items:center;padding:0.75rem 1rem 0.75rem 2.75rem;color:#fff}a.sf-subnav__item:hover{text-decoration:none}.sf-subnav__item.active{background-color:#497cb1}.sf-subnav__item:hover{color:#fff}.sf-subnav__item>.fa{flex-shrink:0;width:1.75rem;opacity:0.3}.sf-subnav__item>span{max-width:100%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:0.875rem}.sf-subnav__item>span:first-child::before{content:'\2022';display:inline-block;margin-right:0.75rem;width:1rem;opacity:0.3;text-align:center}a.sf-nav__item-heading:hover>span{text-decoration:underline}a.sf-subnav__item:hover>span{text-decoration:underline}
