.sf-header{position:-webkit-sticky;position:sticky;z-index:10;top:0;width:100%;height:4rem;padding:0.5rem 1.25rem;display:flex;align-items:center;background-color:#fff;box-shadow:0px 0px 4px rgba(0,0,0,0.05)}.sf-header-user{position:relative;margin-left:auto;flex-shrink:0}.sf-header-user__info{-webkit-appearance:none;-moz-appearance:none;appearance:none;margin:0;padding:0;min-width:8rem;background-color:transparent;border:none;box-shadow:none}.sf-header-user__info:focus{outline:0}.sf-header-user__menu{left:auto;right:0;min-width:8rem;-webkit-animation:menu-show 0.25s ease;animation:menu-show 0.25s ease;font-size:0.875rem}@-webkit-keyframes menu-show{0%{transform:scale(0.9);opacity:0}100%{transform:scale(1);opacity:1}}@keyframes menu-show{0%{transform:scale(0.9);opacity:0}100%{transform:scale(1);opacity:1}}
